import { FC } from "react";
import {
  HStack,
  IconButton,
  StyleProps,
  Tooltip,
  useColorMode,
  useColorModeValue,
} from "@chakra-ui/react";
import { MoonIcon, SunIcon } from "utils/Icons";

export const ColorModeButton: FC<StyleProps> = (props) => {
  const color = useColorModeValue("gray.800", "white");
  const { colorMode, toggleColorMode } = useColorMode();

  const tooltipLabel = colorMode === "light" ? "Dark Mode" : "Light Mode";

  return (
    <HStack {...props}>
      <Tooltip label={tooltipLabel} hasArrow placement="bottom">
        <IconButton
          aria-label="appearance"
          bg="transparent"
          color="primary.500"
          fontSize="lg"
          _hover={{ color: "gray.400" }}
          _active={{ bg: "transparent" }}
          icon={colorMode === "light" ? <MoonIcon /> : <SunIcon />}
          variant="ghost"
          size="xs"
          onClick={() => {
            toggleColorMode();
            window.scrollBy(0, 1);
            window.scrollBy(0, -1);
          }}
          w="32px"
          h="32px"
        />
      </Tooltip>
    </HStack>
  );
};
