import { FC, useRef } from "react";
import { Link as RouterLink } from "react-router-dom";

import {
    Box,
    Button,
    Drawer,
    DrawerBody,
    DrawerHeader,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
    useDisclosure,
    IconButton,
    StyleProps,
    Flex,
    VStack,
    useColorModeValue,
    HStack
} from "@chakra-ui/react";

import { ColorModeButton } from "shared/color-mode-button/ColorModeButton";
import { MenuIcon } from "utils/Icons";
import { Socials } from "shared/socials/Socials";

// 2. Props 인터페이스를 간단하게 수정합니다.
interface Props extends StyleProps {
    currentPage: string; // URL 경로를 받을 prop
}

export const MenuDrawer: FC<Props> = ({ currentPage, ...props }) => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const btnRef = useRef<any>(null);
    const navItemColor = useColorModeValue("gray.800", "white");

    // 3. 불필요한 onSectionClick 관련 코드를 모두 제거합니다.

    return (
        <Box {...props}>
            <HStack spacing="0">
            <ColorModeButton />
            <Button
                as={IconButton}
                variant="icon"
                ref={btnRef}
                onClick={onOpen}
                aria-label="open drawer"
                fontSize="lg"
                color="primary.500"
                icon={<MenuIcon />}
                px="0"
                ml="2"
            />
            </HStack>
            
            <Drawer isOpen={isOpen} placement="right" onClose={onClose} autoFocus={false}>
                <DrawerOverlay />
                <DrawerContent
                maxW={{ base: "60vw", md: "420px", lg: "520px" }}
                
                boxShadow="2xl"
            >
                    <DrawerHeader px="4">
                        <Flex justifyContent="space-between">
                            <DrawerCloseButton position="relative" top="0" right="0" />
                        </Flex>
                    </DrawerHeader>

                    <DrawerBody>
                        <VStack spacing="6" my="16">
                            
                            
                            <Button
                                as={RouterLink}
                                to="/"
                                onClick={onClose} // 링크 클릭 시 드로어를 닫습니다.
                                variant="link"
                                color={navItemColor}
                                // 5. currentPage(URL 경로)를 직접 비교합니다.
                                textDecoration={currentPage === "/" ? "underline" : "none"}
                                textDecorationThickness="2px"
                                textDecorationColor="primary.500"
                                fontWeight="600"
                                fontSize="2xl"
                            >
                                about me
                            </Button>

                            <Button
                                as={RouterLink}
                                to="/projects"
                                onClick={onClose}
                                variant="link"
                                color={navItemColor}
                                textDecoration={currentPage === "/projects" ? "underline" : "none"}
                                textDecorationThickness="2px"
                                textDecorationColor="primary.500"
                                fontWeight="600"
                                fontSize="2xl"
                            >
                                projects
                            </Button>
                            
                            <Button
                                as={RouterLink}
                                to="/publications"
                                onClick={onClose}
                                variant="link"
                                color={navItemColor}
                                textDecoration={currentPage === "/publications" ? "underline" : "none"}
                                textDecorationThickness="2px"
                                textDecorationColor="primary.500"
                                fontWeight="600"
                                fontSize="2xl"
                            >
                                publications
                            </Button>

                            <Button
                                as={RouterLink}
                                to="/etc"
                                onClick={onClose}
                                variant="link"
                                color={navItemColor}
                                textDecoration={currentPage === "/etc" ? "underline" : "none"}
                                textDecorationThickness="2px"
                                textDecorationColor="primary.500"
                                fontWeight="600"
                                fontSize="2xl"
                            >
                                etc.
                            </Button>
                        </VStack>
                        {/* <Flex justifyContent="center" mt="10">
                            <Socials delay={100} resume={false} />
                        </Flex> */}
                    </DrawerBody>
                </DrawerContent>
            </Drawer>
        </Box>
    );
};